    public changeProyects(){
        
        this.addClass("ChagenProyectos", "scrollBottomToTop");
        this.changeText("ChagenProyectos", "I N I C I O");
        this.addClass("content-1", "exitBottomToTop");
        this.showContenresponsive();
        this.changeHeader();
    }
//    private changeClass(id:string, newClass:string){
//         document.getElementById(id).classList.toggle(newClass);
//    }
    private addClass(id:string, newClass:string){
        
         document.getElementById(id).classList.add(newClass);
    }
    private changeText(id:string, newText:string){
         document.getElementById(id).firstElementChild.firstElementChild.innerText = newText;
    }
    private changeHeader(){
        if  (this.titlePage == "Bienvenido"){
            this.titlePage = "Portafolio";
        } else {
            this.titlePage = "Bienvenido";
            this.openHome()
        }
    }
    public openHome() {
    this.navCtrl.setRoot(HomePage);
  }
    private showContenresponsive() {
    if (window.innerWidth > 768){
            this.addClass("content-3", "animationTema-2");
        }else {
            this.addClass("content-2", "animationTema-2");
        }
  }
  itemTapped(event, item) {
    // That's right, we're pushing to ourselves!
    this.navCtrl.push(ListPage, {
      item: item
    });
  }
    
    

